.Overlay{
    position: fixed;
    top:0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 99;
}
.NavMobile{
   position: fixed;
   top: 0;
   right: 0;
   left: 0;
   bottom: 0;
   width: 40vh;
   max-width: 100%;
   background-color: white;
   z-index: 99;
   animation-name: Open;
   animation-duration: 1s;  
   animation-fill-mode: forwards;
}
@keyframes Open {
    from {transform: translateX(-100%);}
    to {transform: translateX(0%);}
  }
.NavList{
    list-style: none;
    margin-top: 50px;
}
.NavLink{
    text-decoration: none;
    text-align: center;
    color: gray;
    font-size: 10px;
    font-weight: bold;
    display: block;
    padding: 20px 0;
    font-family: Arial, Helvetica, sans-serif;
    border-bottom: 1px solid #80808018;
}
.NavLink:hover{
    background-color: #8080802a;
    color: black;
}
.appear{
    animation-name: Close;
    animation-duration: 1s;  
    animation-fill-mode: forwards;
}
@keyframes Close {
    from {transform: translateX(0%);}
    to {transform: translateX(-100%);}
}
.CloseIcon{
    position: fixed;
    top: 0;
    right: 0;
    cursor: pointer;
    z-index: 99;
    font-size: 40px;
    font-weight: bold;
}
.CloseIcon:hover{
    color: gray;
}
.social{
    display: flex;
    justify-content: center;
    margin: 20px 0;
}
.Icon:hover{
    opacity: 0.7;
}
.Facebook{
    color: white;
    background-color:slateblue;
    padding: 6px 9px;
    border-radius: 50%;
    cursor: pointer;
}
.Twitter{
    color: white;
    background-color:skyblue;
    padding: 6px 9px;
    border-radius: 50%;
    margin: 0 10px;
    cursor: pointer;
}
.Youtube{
    color: white;
    background-color:red;
    padding: 6px 9px;
    border-radius: 50%;
    cursor: pointer;
}
.Hotline{
    color: gray;
    text-align: center;
    margin: 20px 0;
    font-size: 15px;
}
.NavRegister{
    cursor: pointer;
}