.title{
    color: #555555;
    text-align: center;
    margin: 30px 0;
}
.ImgAdv{
    object-fit: cover;
    width: 100%;
}
.Content h2{ 
    color: #555555;
    margin-top: 100px;
}
.Content p{
    color: gray;
}
.DetailBtn{
    background-color: #00adee;
    border: 1px solid #00adee;
    color: white;
    font-size: 15px;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    margin: 10px 0;
}
.News{
    margin: 20px 0;
    line-height: 1.5rem;
}
.DetailBtn:hover{
    opacity: 0.7;
}
.NewsTitle{
    color: gray;
    font-size: 15px;
}
.NewsDetail{
    color: gray;
    opacity: 0.7;
}
.News:hover .NewsDetail{
    opacity: 1;
}
.NewsImg{
    width: 250px;
    height: 180px;
    margin-right: 20px;
}
.NewsDateUpdate{
    font-size: 10px;
    color: gray;
}
.BonusNews{
    display: flex;text-decoration: none;
    margin-bottom: 30px;
}
.Partner{
    background-image: linear-gradient(
0deg
, rgba(51,50,41,0.4), rgba(51,50,41,0.4)), url(../img/HomeImg/pic8.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}
.Partner h2{
    color: white;
    text-align: center;
    padding: 30px 0;
}
.Partner p{
    color: white;
    text-align: center;
    padding-bottom: 30px;
    
}
.ImgPartner{
    margin: 30px 20px;
}
.BlockPartner{
    
    text-align: center;
    align-items: center;
    margin: 0 auto;
   
}
.DivImg{
    background-image: url(../img/Partner/9.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    width: 100%;
    height: 500px;
}


