.Logo{
    width: 200px;
    height: 60px;
    margin: 60px 0;
}
.Info{
    margin: 20px 0;
}
.Info h2{
 color: #a4a4a4;
 font-size: 15px;
}
.Info p{
    color: #a4a4a4;
}
.social{
    display: flex;
}
.Icon:hover{
    opacity: 0.7;
}
.Facebook{
    color: white;
    background-color:slateblue;
    padding: 6px 9px;
    border-radius: 50%;
    cursor: pointer;
}
.Twitter{
    color: white;
    background-color:skyblue;
    padding: 6px 9px;
    border-radius: 50%;
    margin: 0 10px;
    cursor: pointer;
}
.Youtube{
    color: white;
    background-color:red;
    padding: 6px 9px;
    border-radius: 50%;
    cursor: pointer;
}
@media screen and (max-width: 739px){
	.Info{
        text-align: center;
    }
    .social{
        margin: 0 auto;
    }
}
