.SearchBlock{
    width: 300px;
    height: 80px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 3px 5px 10px #888888;
}
.SearchInput{
    width: 232px;
    height: 34px;
    margin:22px 2px 0px 5px;
    border: 2px solid gray;
    padding-left: 10px;
}
.SearchIcon{
    background-color: #ed1c24;
    padding: 10px;
    color: white;
    cursor: pointer;
    border-radius: 5px;
}
.SearchIcon:hover{
    background-color: darkred;
}