
.Logo{
    width: 200px;
    height: 60px;
}
.Heading{
    background-color: white;
    padding:20px 0;
}
.NavBars{
    margin: auto;
}
.NavLink{
    text-decoration: none;
    color: black;
    font-weight: bold;
    margin: 20px;
    font-size: 0.7rem;
}
.SearchIcon{
    margin-right: 20px;
    cursor: pointer;
    position: relative;
    margin: auto;
}

.SearchIcon:hover .HeadingSearch{
    display: block;
}
.SearchIcon:hover.SearchIcon::before{
    content: "";
    border: 30px solid;
    border-color: transparent transparent lightgray transparent;
    position: absolute;
    left: -7px;
    top: -3px;
    border-width: 19px 15px;
    display: block;
}

.HeadingSearch{
    position: absolute;
    right: -100px;
    top:33px;
    display: none;
    z-index: 99;
}
.SignBtn{
    background-color: red;
    border: 1px solid red;
    color: white;
    border-radius: 10px;
    font-weight: bold;
    cursor: pointer;
    padding: 0 10px;
}
.SignBtn:hover{
    background-color: darkred;
}
.Cart{
    font-size: 30px;
    margin: auto 10px;
    color: gray;
    display: none;
    cursor: pointer;
}
.Cart:hover{
   color: lightskyblue;
}
.Menu{
    font-size: 30px;
    margin: auto 10px;
    color: gray;
    display: none;
    cursor: pointer;
}

/* Tablet */
@media only screen and (min-width: 46.25em) and (max-width: 63.9375em) {
    .row{
        margin-left: 4px;
        margin-right: 4px;
    }
    .NavBars{
        display: none;
    }
    .SearchIcon{
        display: none;
    }
    .SignBtn{
        display: none;
    }
    .Logo{
        margin: 0 auto;
    }
    .Cart{
        display: block;
    }
    .Menu{
        display: block;
    }
    
}

/* Mobile */
@media only screen and (max-width: 46.1875em) {
    .row{
        margin-left: 4px;
        margin-right: 4px;
    }
    .NavBars{
        display: none;
    }
    .SearchIcon{
        display: none;
    }
    .SignBtn{
        display: none;
    }
    .Logo{
        margin: 0 auto;
    }
    .Cart{
        display: block;
    }
    .Menu{
        display: block;
    }
    
}


