.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    width: 100%;
    height: 550px;
}
  
  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
}

/* Responsive */
/* Tablet */
@media only screen and (min-width: 46.25em) and (max-width: 63.9375em) {
  .each-slide > div {
    width: 100%;
    height: 350px;
}
  
}

/* Mobile */
@media only screen and (max-width: 46.1875em) {
  .each-slide > div {
    width: 100%;
    height: 150px;
  }
  
}