.Title{
    color: gray;
    text-align: center;
}
.BlockNews{
    cursor: pointer;
    background-color: white;
    box-shadow: 0px 0px 4px #888888;
    margin: 10px 20px;
    width: 250px;
    margin: 20px auto;
}
.BlockNews img{
    width: 100%;
}
.BlockNews h2{
    color: gray;
    font-size: 15px;
    margin: 10px;
    text-transform: uppercase;
}
.BlockNews p{
    margin: 10px;
    text-transform: uppercase;
}
.BlockNews:hover{
    box-shadow: 5px 3px 10px #888888;
}