.Modal{
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    position: fixed;
    z-index: 99;
}
.Layout{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
}
.Form{
    width: 450px;
    padding: 50px 0;
    background-color: white;
    margin: 50px auto;
   display: flex;
   flex-direction: column;
   position: relative;
   z-index: 1;
   border-radius: 10px;
   animation-name: open;
  animation-duration: 1s;  
  animation-fill-mode: forwards;
}
@keyframes open{
    from {top: -500px;}
    to {top: 10px;}
}
.FormGroup{
    display: flex;
    flex-direction: column;
}
.FormMessage{
    margin: 0px 26px;
}
.Input{
    width: 400px;
    height: 40px;
    margin: 5px auto;
    padding-left: 10px;
    resize: none;
    border: 1px solid gray;
}
.Description{
    height: 150px;
}
.RegisBtn{
    margin: 20px 26px;
    padding: 10px 0;
    background-color: red;
    color: white;
    border: 1px solid;
    border-radius: 10px;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
}
.RegisBtn:hover{
    background-color: darkred;
}
.Warning{
    border: 1px solid red;
}
.show{
    transition: all 5s linear;
    transform:translate(9999px);
  opacity: 0;
}


.CloseIcon{
    position: absolute;
    top:0;
    right: 0;
    font-size: 35px;
    color: gray;
    cursor: pointer;
}
.CloseIcon:hover{
    color: darkred;
}

/* mobile */
@media only screen and (max-width: 46.1875em){
	.Form{
        width: 300px;
        margin: auto auto;
    }
    .Input{
        width: 270px;
    }
}

/* Tablet */
@media only screen and (min-width: 46.25em) and (max-width: 63.9375em) {
   .Form{
       width: 400px;
       margin: auto auto;
   }
   .Input{
       width: 370px;
   }
    
}
