.HeadingSlide{
    background-image: url(../img/Duhocnhatban/headingImg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}
.SlideGirls{
    animation-name: Open;
    animation-duration: 2s;  
    animation-fill-mode: forwards;
}
.SignImg img{
    cursor: pointer;
}
@keyframes Open {
    from {transform: translateX(-100%);}
    to {transform: translateX(0%);}
}
.title{
    color: #555555;
    text-align: center;
    margin: 30px 0;
}
.ImgAdv{
    object-fit: cover;
    width: 100%;
}
.Content h2{ 
    color: #555555;
    margin-top: 100px;
}
.Content p{
    color: gray;
}
.DetailBtn{
    background-color: #00adee;
    border: 1px solid #00adee;
    color: white;
    font-size: 15px;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
    margin: 10px 0;
}
.DetailBtn:hover{
    opacity: 0.7;
}
.Teacher img{
    width: 100%;
}
.TeacherInfo{
    max-width: 1000px;
    margin: 50px auto;
    background-color: white;
    box-shadow: 4px 6px 20px 0px #888888;
    border-radius: 20px;
    display: flex;
}
.Profile h2{
    color: gray;
    font-size: 40px;
    margin: 10px;
}
.Profile p{
    color: gray;
    font-size: 20px;
    margin: 10px;
}
.Profile ul li{
    color: gray;
    margin: 10px;
    list-style: none;
}
.Partner{
    background-image: linear-gradient(
0deg
, rgba(51,50,41,0.4), rgba(51,50,41,0.4)), url(../img/HomeImg/pic8.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}
.Partner h2{
    color: white;
    text-align: center;
    padding: 30px 0;
}
.Partner p{
    color: white;
    text-align: center;
    padding-bottom: 30px;
    
}
.ImgPartner{
    margin: 30px 20px;
}
.BlockPartner{
    
    text-align: center;
    align-items: center;
    margin: 0 auto;
   
}